@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{margin:0;padding:0}body{margin:0;font-family:"Playfair Display",-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:16px}*{background:#f9f7f1}h1{font-size:5rem}thin-print{color:#313131}news-title{display:block;height:77%;font-weight:700;font-size:30px;text-transform:uppercase;text-align:left;line-height:normal;overflow:clip;margin-bottom:20px}subtitle{font-weight:400;font-size:18px}.news-source{display:block;background:#faebd7;text-decoration:none;color:#000;margin:5px 0;margin-right:auto}.news-image{height:100%;width:100%;display:flex;justify-content:center}.news-image img{width:100%;object-fit:contain;transition:-webkit-filter .5s ease-in-out;transition:filter .5s ease-in-out;transition:filter .5s ease-in-out, -webkit-filter .5s ease-in-out}.loading-text{text-align:center;margin-top:20px;font-size:20px}
.container{border-top:1px solid #dadce0;overflow-x:hidden;overflow-y:clip}.frontpage{position:relative;display:grid;grid-template-columns:repeat(8, 1fr);grid-template-rows:repeat(4, 310px)}.fp-cell{padding:10px;border-right:1px solid #dadce0;border-bottom:1px solid #dadce0}.fp-cell--1{grid-column:1/span 2;grid-row:1/span 4;overflow-y:auto}.fp-cell--2{grid-column:3/span 4;grid-row:1/span 2}.fp-cell--3{grid-column:7/span 2;grid-row:1/span 2;overflow-y:hidden}.fp-cell--3 .fp-cell--X{border-bottom:none}.fp-cell--4{grid-column:3/span 6;grid-row:3/span 2;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 300px)}.fp-cell--4 .fp-sub-cell{padding:10px;border-right:1px solid #dadce0;border-bottom:1px solid #dadce0;padding-bottom:0;padding-top:0;border-bottom:none}.fp-cell--4 .fp-cell--5{grid-row:1/span 2;grid-column:1;padding-left:0 !important}.fp-cell--4 .fp-cell--6{grid-row:1/span 2;grid-column:2}.fp-cell--4 .fp-cell--7{grid-row:1/span 2;padding-right:0 !important;grid-column:3}.fp-item{display:flex;align-items:center;justify-content:center;min-height:200px;height:100%}@media only screen and (max-width: 720px){.frontpage{display:block}.fp-cell--4{display:block}.fp-cell{border-bottom:2px solid #d7d5d5}}
.center-news-wrap{width:100%;height:100%;padding-top:20px}.center-news-wrap .news-image{height:50%}.center-news-wrap .news-details{display:flex;position:relative;height:45%;margin-top:20px;flex-direction:column;overflow:clip;text-overflow:ellipsis}
.other-news-wrap{display:flex;justify-content:space-between;align-items:center}.other-news-wrap .news-details .news-source{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.other-news-wrap img{width:200px}
.side-news-wrap{width:100%;height:100%}.side-news-wrap .news-image{height:50%}.side-news-wrap .news-details{display:flex;position:relative;height:42%;flex-direction:column;overflow:clip;text-overflow:ellipsis}.side-news-wrap .news-details thin-print{font-size:14px}.side-news-wrap .news-details news-title{font-size:18px}
.masthead-box .paper-name-box .paper-name{text-align:center;font-family:"Playfair Display"}.masthead-box .paper-calendar{border:1px solid;text-align:center;font-family:"Playfair Display";border-right-width:12px;border-left-width:12px}
